/* NotFound.css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.not-found-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #f0f0f0;
  position: relative;
  overflow: hidden;
}

.not-found-title {
  font-size: 10rem;
  color: #ff6b6b;
  font-weight: 900;
  margin-bottom: 20px;
}

.not-found-message {
  font-size: 1.5rem;
  color: #444;
  margin-bottom: 40px;
}

.home-link {
  font-size: 1.2rem;
  padding: 10px 20px;
  background-color: #ff6b6b;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.home-link:hover {
  background-color: #ff4b4b;
}

.not-found-animation {
  position: absolute;
  top: -50px;
  left: -50px;
  width: 200px;
  height: 200px;
  background-color: #ff6b6b;
  border-radius: 50%;
  opacity: 0.8;
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0% {
    transform: translateY(0) translateX(0);
  }
  50% {
    transform: translateY(-20px) translateX(20px);
  }
  100% {
    transform: translateY(0) translateX(0);
  }
}

.not-found-animation::before,
.not-found-animation::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  background-color: #ff6b6b;
  opacity: 0.5;
}

.not-found-animation::before {
  width: 150px;
  height: 150px;
  top: 50px;
  right: 50px;
  animation: float 7s ease-in-out infinite reverse;
}

.not-found-animation::after {
  width: 100px;
  height: 100px;
  bottom: 50px;
  left: 50px;
  animation: float 5s ease-in-out infinite;
}
